.main-titel span {
    font-size: 16px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    padding-left: 21px;
}
.content {
    padding: 20px;
}
.content-btn {
    padding-bottom: 20px;
}
.form-box {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.form-box > .form-input {
    margin-right: 170px;
}
.form-btn {
    flex: 1;
}
.input-box {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.input-box > div > span {
    color: #999999;
    font-size: 14px;
    padding-right: 10px;
}
.content-table {
    margin-top: 20px;
    border: 1px solid #f5f5f6;
}
.creation_box {
    width: 514px;
    margin: 0 auto;
    .el-cascader-menu {
        min-width: 172px !important;
        box-sizing: border-box;
        color: #606266;
        border-right: solid 1px #e4e7ed;
    }
    .el-cascader-menu__wrap {
        height: 284px !important;
    }
    .el-cascader-node.in-active-path,
    .el-cascader-node.is-active,
    .el-cascader-node.is-selectable.in-checked-path {
        color: #fb4702;
        font-weight: 400;
    }
    .el-cascader-node {
        position: relative;
        display: flex;
        align-items: center;
        padding: 0 16px 0 16px;
        height: 32px;
        line-height: 32px;
        outline: 0;
    }
    .btn_footer {
        cursor: pointer;
        width: 256px;
        margin: auto;
        margin-top: 108px;
        margin-bottom: 75px;
        text-align: center;
        color: #fff;
        height: 38px;
        line-height: 38px;
        background: #fb4702;
        border-radius: 4px;
        span {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #ffffff;
            line-height: 20px;
        }
        .btn_footer:hover {
            opacity: 0.8;
        }
    }
}
.drawer_box {
    .el-drawer__header {
        span {
            font-size: 16px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #333333;
        }
    }
    .el-drawer__body {
        background: #e8ebf2;
        overflow-y: auto;
        padding-bottom: 81px;
    }
    :focus {
        outline: 0;
    }
    .dra-content {
        padding-bottom: 60px;
    }
}

.my-autocomplete {
    li {
        line-height: normal;
        padding: 7px;
        .name {
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .addr {
            font-size: 12px;
            color: #b4b4b4;
        }
        .highlighted .addr {
            color: #ddd;
        }
    }
}

// 自定义按钮样式
.btn-orange,
.btn-gray {
    cursor: pointer;
    height: 34px;
    font-size: 14px;
    outline: none;
    border: none;
    color: #ffffff;
    border-radius: 4px;
    transition: 0.2s;
    margin-right: 8px;
    padding: 0 20px;
    i {
        padding-right: 3px;
    }
    &:hover {
        opacity: 0.8;
    }
}
.btn-orange {
    background: #fb4702;
}
.btn-gray {
    background: #c2c2c2;
}
// MessageBox 样式
.el-message-box {
    width: 30%;
    border-radius: 0px;
    .el-message-box__header {
        padding: 20px 20px 10px;
    }
    .el-message-box__content {
        padding: 30px 20px;
        border-top: 1px solid rgba(0, 0, 0, 0.06);
        border-bottom: 1px solid rgba(0, 0, 0, 0.06);
    }
    .el-message-box__btns {
        padding: 10px 20px 20px;
        text-align: right;
        box-sizing: border-box;
        .el-button {
            width: 88px;
            height: 32px;
            font-size: 14px;
        }
        .el-button--default:hover {
            background: #fff;
            border: 1px solid #dcdfe6;
            color: #606266;
        }
        .el-button--primary {
            color: #ffffff;
            background: rgba(251, 71, 2, 1);
            border: none;
            &:hover {
                background: rgba(251, 71, 2, 1);
                border: none;
                color: #fff;
            }
        }
    }
    //
    /*
    :focus{
    background-color: #EF9E6F;
    border-color: #EF9E6F;  
  }
       */
}
.el-drawer__header {
    padding: 0 20px;
    margin: 0;
    height: 81px;
    line-height: 81px;
    span {
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #333333;
    }
}
:focus {
    outline: 0;
}

.report-box {
    .el-progress__text {
        font-size: 14px !important;
        width: 100px !important;
    }
}
.flex {
    display: flex;
}
.justify-between {
    justify-content: space-between;
}
.justify-center {
    justify-content: center;
}
.align-center {
    align-items: center;
}
.flex-wrap {
    flex-wrap: wrap;
}
.el-menu {
    background-color: transparent !important;
}
/* 分页重置样式 */
.pagination-box {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pagination-item p {
    color: #999999;
}
.el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #fb4702 !important;
    color: #fff;
}
.el-pagination.is-background .el-pager li:not(.disabled):hover {
    background-color: #fb4702 !important;
    color: #fff !important;
}
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
    margin: 0 5px;
    background-color: #ffffff !important;
    color: #606266;
    min-width: 30px;
    border-radius: 2px;
    border: 1px solid rgba(0, 0, 0, 0.15);
}
.el-pagination__jump {
    margin-left: 24px;
    font-weight: 400;
    color: #999999 !important;
}
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
    height: 32px;
    margin: 0px 8px;
    background-color: #f4f4f5;
    color: #606266;
    min-width: 30px;
    border-radius: 2px;
    display: inline-block;
    width: 32px;
}
/* 分页插入的搜索框样式重置 */
.page-slot {
    display: inline-block;
    text-align: center;
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #999999;
    line-height: 32px;
}
.slot-jump {
    display: inline-block;
    cursor: pointer;
    width: 51px;
    height: 32px;
    line-height: 32px;
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333333;
    text-align: center;
    background: #ffe7dd;
    border-radius: 2px;
    border: 1px solid #fb4702;
}
.el-input.page-jump {
    width: 52px !important;
    height: 32px !important;
    padding: 0 !important;
}
.el-input.page-jump > .el-input__inner {
    -webkit-appearance: none;
    background-color: #fff;
    background-image: none;
    border-radius: 2px;
    color: #606266;
    font-size: inherit;
    height: 100%;
    line-height: 100%;
    display: inline-block;
    outline: 0;
    padding: 0px !important;
    transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.comprehensive {
    .el-tabs__item.is-active {
        color: #036dfd !important;
    }
    .el-tabs__active-bar {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        background-color: #036dfd !important;
        z-index: 1;
        transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
        list-style: none;
    }
    .el-tabs__item:hover {
        color: #036dfd !important;
        cursor: pointer;
    }
    .login-input .el-input__inner {
        -webkit-appearance: none;
        background-color: #fff;
        background-image: none;
        border-radius: 0px;
        border: none;
        border-bottom: 1px solid #d8d8d8;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: inherit;
        height: 40px;
        line-height: 40px;
        outline: 0;
        padding: 0px 0px;
        margin-bottom: 30px;
        transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
        width: 100%;
    }
    .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: #3163c0 !important;
        color: #fff;
    }
    .el-pagination.is-background .el-pager li:not(.disabled):hover {
        background-color: #3163c0 !important;
        color: #fff !important;
    }
    .el-checkbox__input.is-checked .el-checkbox__inner,
    .el-checkbox__input.is-indeterminate .el-checkbox__inner {
        background-color: #409eff !important;
        border-color: #409eff !important;
    }
    // comprehensive
}
